
.upcoming-shipments {
  padding: 0 2rem 4rem;
  margin: auto;
  margin-bottom: 8rem;
  width: 80%;
  max-width: 800px;
  border: 1px solid #bbbbbb;
  border-radius: 10px;
}
.item {
  width: 100%;
}
.product-name {
  font-weight: 600;
}
.total {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    color-stop(33%, #bbbbbb),
    color-stop(0%, rgba(255, 255, 255, 0))
  );
  background-image: linear-gradient(
    to right,
    #bbbbbb 33%,
    rgba(255, 255, 255, 0) 0%
  );
  background-position: bottom;
  background-size: 4px 1px;
  background-repeat: repeat-x;
  margin-bottom: 1rem;
  text-align: left;
}
td {
  text-align: center;
  width: 200px;
  border-bottom: none;
}
tr {
  border-collapse: collapse;
}
.row-title {
  margin: 1.8rem;
}
.flex-grid {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  min-height: 5rem;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
}
.title {
  display: block;
  text-align: left;
  border-bottom: 1px solid #bbbbbb;
  font-weight: 500;
  margin-top: 1rem;
  padding: 1.5rem;
  font-size: 1.4rem;
}
.col {
  -webkit-box-flex: 2;
      -ms-flex: 2;
          flex: 2;
}
@media only screen and (max-width: 600px) {
.col {
    margin: 0 0.8rem;
    width: 125px;
}
}



